body{margin:0;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.checkbox input{position:relative;top:-1px}.wrap{margin:0 20px}@media (min-width:992px){.wrap{margin:0 auto;padding:0 10px;max-width:992px}}header{text-align:center}header h1{margin:0;padding:10px 0;font-size:2em}@media (min-width:576px){.filters{display:flex;justify-content:space-between;align-content:center}}.filters input[type=text]{font-size:1em;padding:5px;width:100%}.filters .sub-filters{margin-top:10px;display:flex;justify-content:space-between;align-items:center;font-size:.8em}@media (min-width:576px){.filters .sub-filters div:last-child{margin-left:20px}}.list{margin-top:20px}.list h2{font-size:1.1em;margin:10px 0}.list ul{list-style:none;margin:0 -3% 0 0;padding:0;font-size:.8em}@media (min-width:576px){.list ul{margin-right:-2%}}@media (min-width:768px){.list ul{margin-right:-1.6%;margin-right:-1.5%}}.list li{padding:10px 0;width:30%;margin:0 3% 10px 0;display:inline-block;text-align:center;background-color:#eee}@media (min-width:576px){.list li{width:23%;margin-right:2%}}@media (min-width:768px){.list li{width:15%;margin-right:1.6%}}@media (min-width:992px){.list li{width:11%;margin-right:1.5%}}.list img{max-height:35px;display:block;margin:0 auto 5px}